.App {
  font-family: sans-serif;
  text-indent: 80px;
}

.btn {
  width: 25px;
}
.custom-select {
  text-align: left;
}
.input {
  width: 70px;
  text-align: right;
  border: 1px solid #aaaaaa;
  border-radius: 3px;
  background: white;
}
.inputunit {
  margin-left: 5px;
  font-size: 11px;
}

.divTable {
  margin-left: 80px;
  margin-right: auto;
  display: table;
  font-size: 14px;
  border: 1px solid #aaaaaa;
  border-radius: 5px;
}
.divTableRow {
  display: table-row;
  background-color: #a7cce8;
}

.divTableRow1 {
  display: table-row;
  height: 4px;
  background-color: #a7cce8;
}
.divTableRow2 {
  display: table-row;
  background-color: #d4d3cd;
}
.divTableRow3 {
  display: table-row;
  background-color: #f7eeba;
}
.divTableCell,
.divTableHead {
  display: table-cell;
  padding: 1px 5px;
}

.divTableBody {
  display: table-row-group;
}
